		//筛选点击
		tabClick(index){
			if(this.filterIndex === index && index !== 2){
				return;
			}
			this.filterIndex = index;
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			uni.showLoading({
				title: '正在加载'
			})
			this.loadData('refresh', 1);
		},
		//显示分类面板
